import {
  GetBuildDynamicDetails
} from '@/net/BuildNet.js'

import {
  Const
} from "@/utils/Const.js"

export default {
  data () {
    return {
      actId: '',
      infoData: {}
    }
  },
  onLoad (params = {}) {
    this.actId = params.actId
    this.initData()
  },
  methods: {
    initData () {
      new GetBuildDynamicDetails({
        actId: this.actId
      }).send().then(res => {
        console.log(res)
        if (res.errCode !== Const.success) {
          this.DifferenceApi.showToast(res.errMsg)
        } else {
          this.infoData = res.data || {}
        }
      })
    }
  },
}
